:root {
  color-scheme: light;
  --color-background: #f6f8fb;
  --color-surface: #ffffff;
  --color-surface-muted: #eef2f9;
  --color-border: #cbd5e1;
  --color-text: #0f172a;
  --color-text-muted: #475569;
  --color-accent: #1d4ed8;
  --color-accent-contrast: #ffffff;
  --header-shadow: 0 1px 2px rgba(15, 23, 42, 0.08);
}
[data-theme="dark"] {
  color-scheme: dark;
  --color-background: #101828;
  --color-surface: #1d2939;
  --color-surface-muted: #111827;
  --color-border: #344054;
  --color-text: #f8fafc;
  --color-text-muted: #cbd5f5;
  --color-accent: #60a5fa;
  --color-accent-contrast: #0f172a;
  --header-shadow: 0 1px 2px rgba(15, 23, 42, 0.42);
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  min-height: 100vh;
  background: var(--color-background);
  color: var(--color-text);
  font-family: var(--font-geist-sans), system-ui, -apple-system, "Segoe UI", sans-serif;
  line-height: 1.6;
  transition: background 0.3s ease, color 0.3s ease;
}
a {
  color: inherit;
}
.site-shell {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.skip-link {
  position: absolute;
  top: -40px;
  left: 1rem;
  background: var(--color-accent);
  color: var(--color-accent-contrast);
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  z-index: 100;
  text-decoration: none;
  transition: top 0.2s ease;
}
.skip-link:focus {
  top: 1rem;
}
.site-header {
  position: sticky;
  top: 0;
  z-index: 50;
  background: var(--color-surface);
  box-shadow: var(--header-shadow);
  border-bottom: 1px solid var(--color-border);
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.75rem 1.5rem;
}
.header-left {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex: 1 1 auto;
  min-width: 0;
}
.student-number {
  font-weight: 700;
  letter-spacing: 0.08em;
  font-size: 0.9rem;
  text-transform: uppercase;
  color: var(--color-accent);
  background: var(--color-surface-muted);
  padding: 0.25rem 0.75rem;
  border-radius: 999px;
  white-space: nowrap;
}
.site-title {
  font-weight: 600;
  font-size: 1.125rem;
  text-decoration: none;
  color: var(--color-text);
  white-space: nowrap;
}
.site-title:focus-visible {
  outline: 3px solid var(--color-accent);
  outline-offset: 4px;
  border-radius: 0.5rem;
}
.header-actions {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.theme-toggle,
.menu-toggle {
  background: var(--color-surface-muted);
  color: var(--color-text);
  border: 1px solid transparent;
  border-radius: 0.75rem;
  padding: 0.4rem 0.75rem;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s ease, border 0.2s ease, transform 0.2s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.menu-toggle {
  min-width: 44px;
  min-height: 44px;
}
.theme-toggle:hover,
.menu-toggle:hover {
  background: var(--color-accent);
  color: var(--color-accent-contrast);
}
.theme-toggle:focus-visible,
.menu-toggle:focus-visible {
  outline: 3px solid var(--color-accent);
  outline-offset: 3px;
}
.site-nav {
  position: absolute;
  top: calc(100% + 0.75rem);
  right: 1.5rem;
  display: none;
  margin-left: auto;
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: 0.75rem;
  box-shadow: 0 16px 32px rgba(15, 23, 42, 0.2);
  min-width: 220px;
  padding: 0.5rem;
}
.site-nav.open {
  display: block;
}
.site-nav ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  gap: 0.25rem;
  padding: 0;
  margin: 0;
}
.nav-link {
  text-decoration: none;
  display: block;
  font-weight: 500;
  color: var(--color-text-muted);
  padding: 0.6rem 0.9rem;
  border-radius: 0.6rem;
  transition: background 0.2s ease, color 0.2s ease, transform 0.2s ease;
}
.nav-link:hover,
.nav-link:focus-visible {
  color: var(--color-text);
  background: var(--color-surface-muted);
}
.nav-link.active {
  background: var(--color-accent);
  color: var(--color-accent-contrast);
}
.site-main {
  flex: 1 1 auto;
  padding: 2rem 1.5rem 3rem;
  width: min(1100px, 100%);
  margin: 0 auto;
}
.page-heading {
  margin-top: 0;
  font-size: clamp(1.75rem, 2vw + 1rem, 2.5rem);
  margin-bottom: 1rem;
}
.section-heading {
  font-size: 1.25rem;
  margin-top: 2rem;
  margin-bottom: 1rem;
}
.site-footer {
  background: var(--color-surface);
  border-top: 1px solid var(--color-border);
  padding: 1.5rem;
  text-align: center;
  font-size: 0.95rem;
  color: var(--color-text-muted);
}
.site-footer p {
  margin: 0.4rem 0;
}
.card {
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 12px 24px rgba(15, 23, 42, 0.08);
}
.form-grid {
  display: grid;
  gap: 1.5rem;
}
.field-group {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}
.field-group label {
  font-weight: 600;
}
input[type="text"],
textarea,
select {
  width: 100%;
  padding: 0.65rem 0.75rem;
  border-radius: 0.75rem;
  border: 1px solid var(--color-border);
  background: var(--color-surface);
  color: var(--color-text);
  font-size: 1rem;
  transition: border 0.2s ease, box-shadow 0.2s ease;
}
textarea {
  min-height: 140px;
  resize: vertical;
}
input:focus-visible,
textarea:focus-visible,
select:focus-visible {
  outline: 3px solid var(--color-accent);
  outline-offset: 2px;
  border-color: transparent;
}
.button-row {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}
button.primary,
button.secondary {
  border: none;
  border-radius: 0.75rem;
  padding: 0.65rem 1.1rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}
button.primary {
  background: var(--color-accent);
  color: var(--color-accent-contrast);
  box-shadow: 0 10px 18px rgba(37, 99, 235, 0.25);
}
button.secondary {
  background: var(--color-surface-muted);
  color: var(--color-text);
}
button.primary:hover,
button.secondary:hover {
  transform: translateY(-1px);
}
button.primary:disabled,
button.secondary:disabled {
  opacity: 0.55;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}
button.primary:focus-visible,
button.secondary:focus-visible {
  outline: 3px solid var(--color-accent);
  outline-offset: 3px;
}
.tab-list-preview {
  display: grid;
  gap: 0.75rem;
  margin-top: 1.5rem;
}
.tab-item {
  border: 1px solid var(--color-border);
  border-radius: 0.75rem;
  padding: 1rem;
  background: var(--color-surface);
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}
.tab-item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.75rem;
  flex-wrap: wrap;
}
.tab-item-actions {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}
.tab-item button {
  padding-inline: 0.6rem;
  padding-block: 0.4rem;
}
.generated-output {
  margin-top: 1.5rem;
}
.generated-output textarea {
  min-height: 280px;
  font-family: var(--font-geist-mono), SFMono-Regular, Consolas, "Liberation Mono", monospace;
}
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}
@media (max-width: 960px) {
  .site-nav ul {
    gap: 0.25rem;
  }
  .nav-link {
    padding: 0.45rem 0.75rem;
  }
}
@media (max-width: 768px) {
  .site-header {
    flex-wrap: wrap;
    align-items: flex-start;
  }
  .header-left {
    width: 100%;
    justify-content: space-between;
  }
  .header-actions {
    order: 3;
    width: 100%;
    justify-content: flex-end;
  }
  .menu-toggle {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 44px;
    min-height: 44px;
  }
  .site-nav {
    order: 4;
    position: static;
    width: 100%;
    margin-top: 0.75rem;
    box-shadow: 0 12px 24px rgba(15, 23, 42, 0.1);
  }
}
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
